.Character {
  background-color: #655136;
  border-radius: 4px;
  border: 2px solid #222222;
  margin: 1rem;
  padding: 1rem;
  width: 200px; }
  .Character:hover {
    cursor: pointer; }
  .Character.dead {
    border-color: red;
    opacity: 0.3; }
    .Character.dead:hover {
      cursor: not-allowed; }
  .Character.selected {
    outline: thick double #32a1ce; }
  .Character .header {
    text-align: center;
    text-decoration: underline; }
  .Character .content .image {
    margin: 1rem 0;
    width: 100%; }
  .Character .footer {
    align-items: center;
    display: flex; }
    .Character .footer .healthBar {
      appearance: none;
      -webkit-appearance: none;
      flex-grow: 5;
      height: 1.5rem;
      margin: 0.5rem 0; }
    .Character .footer .healthText {
      flex-grow: 1;
      margin: 0 0.5rem; }



