.EndOfGameView {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center; }
  .EndOfGameView.victory {
    color: #007bff; }
  .EndOfGameView.loss {
    color: #dc3545; }
  .EndOfGameView.ratio {
    background-color: #eeeeee;
    border: 1px solid #6c757d;
    border-radius: 4px;
    color: #222222; }
  .EndOfGameView div {
    padding: 1rem 0; }
  .EndOfGameView button {
    width: 200px; }



